import {mapState,mapMutations} from 'vuex';
export default {
	components: {
		
	},
	data() {
		return {
			passwordType:'',
			pwd:'',
			newPwd:'',
			reNewPwd:''
			
		}
	},
	computed: mapState(['token']),
	onShow(){
		// this.getShopcar();
	},
	onLoad(e) {
		let passwordType = e['passwordType'];
		this.passwordType = passwordType;
		if(passwordType == 2) {
			uni.setNavigationBarTitle({
				title: '修改安全密码'
			})
		}
	},
	methods: {
		requestModifyPsd(data){ //获取用户地址列表
			uni.request({
				url: this.websiteUrl + 'user/pwd_edit',
				method: 'POST',
				data:data,
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						// console.log("密码修改成功："+JSON.stringify(res.data));
						uni.showToast({
							icon: 'success',
							title: '密码修改成功',
							duration:2000,
							success:function(){
								setTimeout(()=>{
									uni.navigateBack({
										delta:1
									})
								},2000)
							}
						});
						
					}else if (res.code == 401) {
						uni.navigateTo({
							url:'/pages/login/login/login'
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: res.message,
							duration:2000
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		modifyPassword(){
			let pwd = this.pwd;
			let newPwd = this.newPwd;
			let reNewPwd = this.reNewPwd;
			let passwordType = this.passwordType;
			var data = {
				pwd:pwd,
				newPwd:newPwd,
				reNewPwd:reNewPwd,
				type:passwordType
			}
			if(newPwd != reNewPwd){
				uni.showToast({
					icon: 'none',
					title: '两次输入的新密码不一致',
					duration:2000
				});
				return false;
			}
			if(passwordType == 2){
				if(this.customMethods.six_num_ver(newPwd)==0){
					uni.showToast({
						icon: 'none',
						title: '请输入6位数字',
						duration:2000
					});
					return false;
				}else {
					this.requestModifyPsd(data)
				}
			}else {
				this.requestModifyPsd(data)
			}
		}
	}
}
